
.all .items {
  width: 700px;
  border-top: 1px solid #efefef;
  margin: 60px auto 0;
}
.all .items table {
  margin-top: 40px;
  width: 100%;
  font-size: 14px;
}
.all .items table thead th {
  line-height: 30px;
}
.all .items table thead th:nth-child(1) {
  width: 60%;
}
.all .items table thead th:nth-child(2) {
  width: 40%;
}
.all .items table tbody {
  background: #fff;
}
.all .items table tbody tr {
  border-bottom: 1px solid #c9c3be;
}
.all .items table tbody tr:hover {
  background: #fcf0e5;
}
.all .items table tbody tr td {
  line-height: 40px;
  text-align: center;
}


