
.single {
  position: relative;
  padding: 10px 20px 40px;
}
.single:hover {
  background: #fdf1e6;
}
.single:hover .direct {
  display: block;
}
.single h2 {
  height: 30px;
}
.single h2 div {
  float: left;
  height: 30px;
  line-height: 30px;
  font-size: 16px;
}
.single h2 div:nth-child(1) {
  width: 6%;
}
.single h2 div:nth-child(2) {
  width: 94%;
}
.single h2 div:nth-child(2) p {
  width: 100%;
}
.single h2 div:nth-child(2) input {
  display: block;
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
}
.single .options {
  padding-left: 30px;
}
.single .options .optionItemContainer {
  width: 100%;
  height: 30px;
}
.single .options .optionItemContainer:hover .optionsDelBtn {
  display: block;
}
.single .options .optionItemContainer input[type=radio] {
  float: left;
  width: 4%;
  margin-top: 8px;
}
.single .options .optionItemContainer .optionContent {
  float: left;
  width: 93%;
  height: 30px;
  line-height: 30px;
}
.single .options .optionItemContainer .optionContent p {
  width: 100%;
  height: 100%;
}
.single .options .optionItemContainer .optionContent input {
  display: block;
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
}
.single .options .optionItemContainer .optionsDelBtn {
  display: none;
  float: left;
  width: 3%;
  height: 30px;
  line-height: 30px;
  text-align: center;
  cursor: pointer;
}
.single .direct {
  position: absolute;
  display: none;
  left: 3%;
  bottom: 4px;
  width: 94%;
  height: 20px;
}
.single .direct span {
  cursor: pointer;
}
.single .direct .left {
  float: left;
}
.single .direct .right {
  float: right;
  margin-left: 10px;
}


