
.textSimple {
  position: relative;
  padding: 10px 20px 10px;
}
.textSimple:hover {
  background: #fdf1e6;
}
.textSimple h2 {
  height: 30px;
}
.textSimple h2 div {
  float: left;
  height: 100%;
  font-size: 16px;
  line-height: 30px;
}
.textSimple h2 div:nth-child(1) {
  width: 6%;
}
.textSimple h2 div:nth-child(2) {
  width: 94%;
}
.textSimple h2 div span {
  color: #ee7419;
}
.textSimple .textContainer {
  margin-top: 10px;
  padding-left: 34px;
}
.textSimple .textContainer textarea {
  padding: 10px;
  resize: none;
  outline: none;
}


