
.singleSimple {
  padding: 10px 20px 10px;
}
.singleSimple:hover {
  background: #fdf1e6;
}
.singleSimple h2 {
  height: 30px;
}
.singleSimple h2 div {
  float: left;
  height: 30px;
  line-height: 30px;
  font-size: 16px;
}
.singleSimple h2 div:nth-child(1) {
  width: 6%;
}
.singleSimple h2 div:nth-child(2) {
  width: 94%;
}
.singleSimple .options {
  padding-left: 30px;
}
.singleSimple .options .optionItemContainer {
  width: 100%;
  height: 30px;
}
.singleSimple .options .optionItemContainer input[type=radio] {
  float: left;
  width: 4%;
  margin-top: 8px;
}
.singleSimple .options .optionItemContainer .optionContent {
  float: left;
  width: 96%;
  height: 30px;
  line-height: 30px;
}


