
.user .createBar {
  margin: 100px auto 0;
  width: 700px;
  height: 200px;
  background: #fff;
  border-top: 1px solid #fff;
}
.user .createBar a {
  display: block;
  width: 120px;
  height: 20px;
  margin: 85px 0 0 285px;
  padding: 5px;
  text-align: center;
  background: #f07600;
  color: #fff;
}
.user .createBar a span {
  font-size: 14px;
  line-height: 20px;
}
.user .createBar a span:nth-child(1) {
  margin-right: 10px;
}
.user .items {
  width: 700px;
  border-top: 1px solid #efefef;
  margin: 60px auto 0;
}
.user .items .create {
  display: block;
  float: right;
  width: 120px;
  height: 20px;
  padding: 5px;
  text-align: center;
  background: #f07600;
  color: #fff;
}
.user .items .create span {
  font-size: 14px;
  line-height: 20px;
}
.user .items .create span:nth-child(1) {
  margin-right: 10px;
}
.user .items table {
  margin-top: 40px;
  width: 100%;
  font-size: 14px;
}
.user .items table thead th {
  line-height: 30px;
}
.user .items table thead th:nth-child(1) {
  width: 7%;
}
.user .items table thead th:nth-child(2) {
  width: 20%;
}
.user .items table thead th:nth-child(3) {
  width: 30%;
}
.user .items table thead th:nth-child(4) {
  width: 10%;
}
.user .items table thead th:nth-child(5) {
  width: 33%;
}
.user .items table tbody {
  background: #fff;
}
.user .items table tbody tr {
  border-bottom: 1px solid #c9c3be;
}
.user .items table tbody tr:hover {
  background: #fcf0e5;
}
.user .items table tbody tr.selected {
  background: #fcf0e5;
}
.user .items table tbody tr td {
  line-height: 40px;
  text-align: center;
}
.user .items table tbody tr td.active {
  color: #ee7419;
}
.user .items table tbody tr td a {
  display: inline-block;
  line-height: 20px;
  padding: 2px 6px;
  color: #747474;
  border: 1px solid #c9c3be;
  border-radius: 3px;
}
.user .items table tbody tr td a.cantedit {
  opacity: 0.6;
}
.user .items .deleteAll {
  background: #fff;
  padding: 10px 0 50px 18px;
  font-size: 14px;
  line-height: 20px;
}
.user .items .deleteAll input {
  margin-right: 6px;
}
.user .items .deleteAll a {
  display: inline-block;
  margin-left: 20px;
  padding: 0 10px;
  border: 1px solid #c9c3be;
  border-radius: 3px;
  color: #747474;
}


