
#header {
  height: 20px;
  padding: 10px 0 10px 40px;
  font-size: 14px;
  line-height: 20px;
  background: #ee7419;
}
#header .mark {
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 10px;
  text-align: center;
  font-weight: bolder;
  color: #ee7419;
  background: #fff;
}
#header .title {
  color: #fff;
  font-weight: bolder;
}
#header .myQuestionaire {
  margin-left: 60px;
  color: #fff;
}
#header .logGroup {
  display: inline-block;
  float: right;
  width: 100px;
  text-align: center;
  color: #fff;
  cursor: pointer;
}
#header .logGroup a {
  color: #fff;
}
#header .logGroup .logOutGroup {
  position: relative;
}
#header .logGroup .logOutGroup .panel {
  padding: 6px 0;
  position: absolute;
  top: 34px;
  left: 0;
  width: 100px;
  z-index: 999;
  background: #ee7419;
  border-radius: 4px;
}
#header .logGroup .logOutGroup .panel a {
  display: block;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  cursor: pointer;
}
#header .logGroup .logOutGroup .panel a:nth-child(1) {
  border-bottom: 1px solid #fff;
}


