
.update {
  width: 660px;
  padding: 20px;
  margin: 40px auto;
  background: #fff;
}
.update .createHeader h1 {
  margin: 20px;
  height: 40px;
  font-size: 18px;
  line-height: 40px;
  text-align: center;
  font-weight: bolder;
  color: #65584f;
}
.update .createHeader h1:hover {
  background: #fef1e8;
}
.update .createHeader input {
  dispaly: block;
  width: 94%;
  height: 40px;
  margin: 20px;
  font-size: 18px;
  line-height: 40px;
  text-align: center;
  font-weight: bolder;
  border: none;
  outline: none;
}
.update .createBody {
  padding: 20px;
  border-top: 1px solid #aeaeae;
  border-bottom: 1px solid #aeaeae;
}
.update .createBody .createBtn {
  position: relative;
  height: 60px;
  margin-top: 20px;
  border: 1px solid #cbcbcb;
}
.update .createBody .createBtn .questionOption {
  height: 60px;
  text-align: center;
}
.update .createBody .createBtn .questionOption span {
  display: inline-block;
  padding: 4px 10px;
  margin: 19px 10px;
  background: #eee;
  border: 1px solid #cbcbcb;
  cursor: pointer;
}
.update .createBody .createBtn .newQuestion {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 60px;
  font-size: 16px;
  text-align: center;
  line-height: 60px;
  background: #eee;
  color: #6e6e6e;
  transition: 0.4s;
  cursor: pointer;
}
.update .footerContainer {
  position: relative;
  padding: 20px 60px;
  height: 30px;
  color: #6a6a6a;
}
.update .footerContainer .calander {
  float: left;
  width: 50%;
  height: 30px;
}
.update .footerContainer .calander span {
  display: block;
  float: left;
}
.update .footerContainer .calander span:nth-child(1) {
  width: 90px;
  height: 30px;
  line-height: 30px;
}
.update .footerContainer .calander span:nth-child(2) {
  width: 140px;
  height: 28px;
  line-height: 28px;
  border: 1px solid #ced1d2;
  text-indent: 10px;
  cursor: pointer;
}
.update .footerContainer .saveGroup {
  float: left;
  width: 50%;
  height: 30px;
  line-height: 30px;
  text-align: center;
}
.update .footerContainer .saveGroup span {
  padding: 3px 10px;
  margin: 0 10px;
  border-radius: 3px;
  cursor: pointer;
}
.update .footerContainer .saveGroup span:nth-child(1) {
  border: 1px solid #ced1d2;
}
.update .footerContainer .saveGroup span:nth-child(2) {
  background: #f07600;
  color: #fff;
  border: 1px solid #f07600;
}
.update .footerContainer .calanderhook {
  height: 0;
  width: 0;
  position: absolute;
  left: 150px;
  top: 60px;
  z-index: 999;
}


