
.singleDataPanel {
  padding: 10px 20px 20px;
}
.singleDataPanel:hover {
  background: #fdf1e6;
}
.singleDataPanel h2 {
  height: 30px;
}
.singleDataPanel h2 div {
  float: left;
  height: 30px;
  font-size: 16px;
  line-height: 30px;
}
.singleDataPanel h2 div:nth-child(1) {
  width: 6%;
}
.singleDataPanel h2 div:nth-child(2) {
  width: 94%;
}
.singleDataPanel h2 div:nth-child(2) p {
  width: 100%;
}
.singleDataPanel .graph {
  border: 1px solid #666;
}


