
.questionaire {
  width: 660px;
  padding: 20px;
  margin: 40px auto;
  background: #fff;
}
.questionaire .questionaireHeader h1 {
  margin: 20px;
  height: 40px;
  font-size: 18px;
  line-height: 40px;
  text-align: center;
  font-weight: bolder;
  color: #65584f;
}
.questionaire .questionaireHeader h1:hover {
  background: #fef1e8;
}
.questionaire .questionaireBody {
  padding: 20px;
  border-top: 1px solid #aeaeae;
  border-bottom: 1px solid #aeaeae;
}
.questionaire .questionnaireFooter {
  padding: 20px 0;
}
.questionaire .questionnaireFooter span {
  display: block;
  margin: 0 auto;
  width: 140px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  border: 1px solid #aeaeae;
  cursor: pointer;
}


