
.calanderContainer {
  width: 210px;
  border: 1px solid #ced1d2;
  border-radius: 4px;
}
.calanderContainer .calanderHeader {
  width: 210px;
  height: 30px;
  line-height: 30px;
  text-align: center;
}
.calanderContainer .calanderHeader span {
  margin: 0 10px;
}
.calanderContainer .calanderWeek {
  height: 30px;
  border-top: 1px solid #ced1d2;
  border-bottom: 1px solid #ced1d2;
  font-size: 0px;
}
.calanderContainer .calanderWeek span {
  display: inline-block;
  width: 30px;
  height: 30px;
  font-size: 14px;
  text-align: center;
  line-height: 30px;
}
.calanderContainer .calanderDate {
  width: 210px;
  height: 180px;
  padding: 4px 0;
  font-size: 0px;
}
.calanderContainer .calanderDate .date {
  display: inline-block;
  width: 30px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  font-size: 14px;
  cursor: pointer;
}
.calanderContainer .calanderDate .date:hover {
  background: #ced1d2;
  color: #fff;
}


