
.mask {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: #000;
  opacity: 0.6;
  z-index: 9;
}
.info-panel {
  position: fixed;
  width: 240px;
  height: 180px;
  left: 50%;
  top: 30%;
  margin: 0 -150px;
  padding: 0px 30px 10px;
  background: #fff;
  border-radius: 5px;
  z-index: 99;
}
.info-panel .panel-header {
  font-size: 14px;
  line-height: 40px;
  background: #f7f7f7;
}
.info-panel .panel-header span:nth-child(1) {
  font-weight: bolder;
}
.info-panel .panel-header span:nth-child(2) {
  float: right;
  cursor: pointer;
}
.info-panel .panel-body p:nth-child(1) {
  padding: 20px 0 40px;
  line-height: 24px;
}
.info-panel .panel-body p:nth-child(2) {
  text-align: center;
}
.info-panel .panel-body p:nth-child(2) span {
  display: inline-block;
  margin: 0 20px;
  padding: 4px 8px;
  border-radius: 4px;
  cursor: pointer;
}
.info-panel .panel-body p:nth-child(2) span:nth-child(1) {
  background: #f07600;
  border: 1px solid #f07600;
  color: #fff;
}
.info-panel .panel-body p:nth-child(2) span:nth-child(2) {
  border: 1px solid #a7a7a7;
}


