
.text {
  position: relative;
  padding: 10px 20px 40px;
}
.text:hover {
  background: #fdf1e6;
}
.text:hover .direct {
  display: block;
}
.text h2 {
  height: 30px;
}
.text h2 div {
  float: left;
  height: 100%;
  font-size: 16px;
  line-height: 30px;
}
.text h2 div:nth-child(1) {
  width: 6%;
}
.text h2 div:nth-child(2) {
  width: 70%;
}
.text h2 div:nth-child(2) p {
  width: 100%;
}
.text h2 div:nth-child(2) input {
  display: block;
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
}
.text h2 div:nth-child(3) {
  width: 24%;
  text-align: right;
}
.text .textContainer {
  margin-top: 10px;
  padding-left: 34px;
}
.text .textContainer textarea {
  padding: 10px;
  resize: none;
  outline: none;
}
.text .direct {
  position: absolute;
  display: none;
  left: 3%;
  bottom: 4px;
  width: 94%;
  height: 20px;
}
.text .direct span {
  cursor: pointer;
}
.text .direct .left {
  float: left;
}
.text .direct .right {
  float: right;
  margin-left: 10px;
}


