
.logContainer {
  margin: 200px auto;
  width: 600px;
}
.logContainer .register {
  float: left;
  width: 199px;
  padding: 50px;
  border-right: 1px dashed #999;
}
.logContainer .login {
  float: left;
  width: 200px;
  padding: 50px;
}
.logContainer label {
  font-size: 16px;
  line-height: 30px;
  font-weight: 500;
}
.logContainer input {
  width: 200px;
  font-size: 14px;
  line-height: 24px;
  text-indent: 10px;
  outline: none;
}
.logContainer .btn {
  width: 40px;
  margin-top: 10px;
  border-radius: 5px;
  text-align: center;
  background: #ee7419;
  color: #fff;
  text-indent: 0;
}


